import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { ApiService } from '../../../apiservice/api.service';
import { urls } from '../../../urls/urls';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  constructor(private apiService: ApiService) { }

  getVerificationStatus(): Observable<any> {
    return this.apiService.post(urls.latestToken, {});
  }

  saveProfile(data): Observable<any> {
    return this.apiService.post(urls.saveProfile, data);
  }

  updateProfile(data, id): Observable<any> {
    return this.apiService.post(urls.updateProfile(id), data);
  }

  viewProfile(id): Observable<any> {
    return this.apiService.post(urls.viewProfile(id), {});
  }

  saveIds(data): Observable<any> {
    let url = '';
    if (data.params.id) {
      url = urls.updateId(data.params.id);
    } else {
      url = urls.saveIds;
    }
    return this.apiService.post(url, data);
  }

  saveRefs(data): Observable<any> {
    let url = '';
    if (data.params.id) {
      url = urls.updateReference(data.params.id);
    } else {
      url = urls.saveReference;
    }
    return this.apiService.post(url, data);
  }

  saveCertificates(data): Observable<any> {
    let url = '';
    if (data.params.id) {
      url = urls.updateCertificates(data.params.id);
    } else {
      url = urls.saveCertificates;
    }
    return this.apiService.post(url, data);
  }

  getCountry(): Observable<any> {
    const requestData = {
      params: {
        domain: `[('id','!=',False)]`,
        fields: `['name','id']`
      }
    }
    return this.apiService.post(urls.country, requestData);
  }

  getStates(countryId: number): Observable<any> {
    const requestData = {
      params: {
        domain: `[('country_id','=',${countryId})]`,
        fields: `['name','id']`
      }
    }
    return this.apiService.post(urls.state, requestData);
  }

  getCities(stateId: number): Observable<any> {
    const requestData = {
      params: {
        domain: `[('state_id','=',${stateId})]`,
        fields: `['name','id']`
      }
    }
    return this.apiService.post(urls.city, requestData);
  }

  getIdProofTypes(): Observable<any> {
    const requestData = {
      params: {
        domain: `[('id','!=',False)]`,
        fields: `['name','id']`
      }
    }
    return this.apiService.post(urls.proofType, requestData);
  }

  getCourseTypes(): Observable<any> {
    const requestData = {
      params: {
        domain: `[('id','!=',False)]`,
        fields: `['name','id']`
      }
    }
    return this.apiService.post(urls.course, requestData);
  }

  getLanguages(): Observable<any> {
    const requestData = {
      params: {
        domain: `[('id','!=',False)]`,
        fields: `['name','id']`
      }
    }
    return this.apiService.post(urls.languages, requestData);
  }

}
